.actions {
	.title_36 {
		margin-bottom: rem(22);
	}

	.page_control {
		margin-top: rem(64);
	}
}

.action_item {
	height: rem(422);
	overflow: hidden;

	display: flex;
	margin-bottom: rem(46);


}

.action_item_first {
	.left {
		position: relative;
		flex: 0 0 72%;
		max-width: 72%;

		display: flex;
		align-items: center;
		justify-content: center;

		&::before {
			content: "";
			position: absolute;
			top: rem(-19);
			right: rem(-200);

			width: rem(442);
			height: rem(442);
			border-radius: 50%;
			background-color: #FFB571;
			z-index: -2;
		}

		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;

			z-index: -1;
		}

		&__count {
			display: flex;
			align-items: center;
			justify-content: center;

			width: rem(126);
			height: rem(126);
			border-radius: 50%;
			background-color: black;

			font-weight: 800;;
			font-size: rem(26);
			color: white;
		}
	}

	.right {
		flex: 0 0 28%;
		max-width: 28%;

		padding: rem(46) rem(68);

		display: flex;
		flex-direction: column;

		p {
			font-size: rem(24);
			line-height: 160%;
			font-weight: 500;
			color: black;
			margin-bottom: rem(28);

			@include min1440 {
				font-size: rem(20);
			}
		}

		&__count {
			padding: rem(2) rem(13);
			align-self: flex-start;
			display: flex;
			align-items: center;

			font-size: rem(17);
			line-height: 156%;
			border-radius: rem(60);
			border: rem(0.5) solid black;
		}

		&__price {
			display: flex;
			flex-direction: column;

			b {
				font-size: rem(24);
				line-height: 140%;
			}

			span {
				color: var(--c-black-50);
				font-weight: 500;
				line-height: 140%;
				text-decoration: line-through;
			}
		}

		.button_black {
			align-self: flex-start;
			margin-top: rem(44);

			padding: 0 rem(50);
			height: rem(50);
			background-color: black;
			border: rem(1) solid black;
			color: white;
			font-weight: 500;

			display: flex;
			align-items: center;
			justify-content: center;

			&:hover {
				background-color: transparent;
				border: rem(1) solid black;

				color: black;
			}
		}
	}
}

.action_item_center {
	position: relative;
	background-color: #323232;
	
	.left {
		position: relative;
		z-index: 1;
		padding: rem(75) rem(45) rem(45) rem(75);

		display: flex;
		flex-direction: column;

		.title {
			font-family: var(--fontSpartan);
			font-weight: 800;
			color: white;
			text-transform: uppercase;
			font-size: rem(26);
			margin-bottom: rem(14);

			@include min1440 {
				font-size: rem(22);
			}
		}

		.subtitle {
			font-family: var(--fontSpartan);
			font-size: rem(18);
			color: white;
			margin-bottom: rem(40);
		}

		&__count {
			padding: rem(2) rem(13);
			align-self: flex-start;
			display: flex;
			align-items: center;
			color: white;
			font-size: rem(17);
			line-height: 156%;
			border-radius: rem(60);
			border: rem(0.5) solid white;
		}

		&__price {
			display: flex;
			flex-direction: column;

			b {
				font-size: rem(24);
				line-height: 140%;
				color: white;
				white-space: nowrap;
			}

			span {
				color: var(--c-white-50);
				font-weight: 500;
				line-height: 140%;
				text-decoration: line-through;
			}
		}

		&__list {
			margin-left: rem(32);
			padding-left: rem(46);

			border-left: rem(1) solid white;

			li {
				list-style: disc;
				white-space: nowrap;
				font-size: rem(18);
				line-height: 152%;
				color: white;
				font-family: var(--fontSpartan);
			}
		}

		&__between {
			display: flex;
			flex-direction: column;
		}

		.button_white {
			margin-top: rem(62);

			align-self: flex-start;

			padding: 0 rem(50);
			height: rem(50);
			background-color: white;
			border: rem(1) solid white;
			color: black;
			font-weight: 500;

			display: flex;
			align-items: center;
			justify-content: center;

			&:hover {
				background-color: black;
				border: rem(1) solid black;

				color: white;
			}
		}

	}

	.right {
		// position: relative;
		display: flex;
		align-items: center;
		&__radio {
			position: absolute;
			top: 0;
			right: 0;
			z-index: 0;
		}
		img {
			position: relative;
			z-index: 1;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&__text {
			position: absolute;
			top: 50%;
			margin-top: rem(-45);
			right: rem(315);
			z-index: 2;
			text-shadow: 0px 13.2553px 16.5692px rgba(0, 0, 0, 0.2),
			0px 13.2553px 165.692px rgba(0, 0, 0, 0.4);

			font-weight: 800;
			color: white;
			font-size: rem(90);
		}
	}
}

.action_item_last {
	height: rem(500);

	.left {
		flex: 0 0 50%;
		max-width: 50%;
		position: relative;
		background-color: #FFB571;

		display: flex;
		align-items: center;
		justify-content: center;

		overflow: hidden;

		&__elip1 {
			position: absolute;
			top: 50%;
			margin-top: rem(-76);
			left: 0;
			z-index: 2;

			width: rem(158);
			height: rem(158);
		}

		&__elip2 {
			position: absolute;
			top: rem(-103);
			right: rem(-42);
			width: rem(300);
			height: rem(300);

			display: flex;
			align-items: center;
			justify-content: center;

			border-radius: 50%;
			border: rem(2) solid white;

			

			span {
				font-family: var(--fontBrygada);
				font-style: italic;
				font-weight: bold;
				color: white;
				font-size: rem(21);
				text-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2),
				0px 4px 50px rgba(0, 0, 0, 0.4);
				transform: rotate(-28.91deg);
				max-width: rem(164);
			}
		}

		&__elip3 {
			position: absolute;
			bottom: rem(-32);
			right: rem(-32);
			width: rem(158);
			height: rem(158);

			display: flex;
			align-items: center;
			justify-content: center;

			border-radius: 50%;
			border: rem(2) solid white;

			span {
				font-family: var(--fontBrygada);
				font-style: italic;
				font-weight: bold;
				color: white;
				font-size: rem(21);
				text-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2),
				0px 4px 50px rgba(0, 0, 0, 0.4);
				max-width: rem(103);
			}

		}

		.button_black {
			position: absolute;
			bottom: rem(65);
			display: flex;
			align-items: center;
			height: rem(75);
			border: rem(1) solid black;
			border-radius: rem(150);
			padding: 0 rem(34);

			&:hover {
				background-color: transparent;
				border: rem(1) solid black;
				
				span {
					color: black;
				}
			}

			span {
				font-size: rem(21);
				font-weight: 800;
				color: white;
			}
		}
	}

	.right {
		flex: 0 0 50%;
		max-width: 50%;

		display: flex;
		flex-direction: column;

		padding: rem(56) rem(165) rem(56) rem(100);

		@include min1440 {
			padding: rem(56) rem(120) rem(56) rem(100);
		}

		.title {
			margin-bottom: rem(20);
			font-size: rem(36);
			line-height: 160%;
			font-weight: bold;
			color: black;

			@include min1440 {
				font-size: rem(32);
			}
		}

		ul {
			margin-bottom: rem(36);
			li {
				line-height: 160%;
				font-weight: 600;
				
				p {
					font-size: rem(12);
					line-height: 108%;
				}
			}

		}

		.button_black {
			align-self: flex-start;
			display: flex;
			align-items: center;
			height: rem(50);
			border: rem(1) solid black;
			background-color: black;
			color: white;
			font-weight: 500;
			padding: 0 rem(50);

			&:hover {
				background-color: transparent;
				border: rem(1) solid black;
				color: black;
			}
		}
	}
}